
















    import Vue from 'vue';
    import { Component, Watch, Prop } from 'vue-property-decorator';
    import {DisplayWeapon} from '../../ts/view-model/weapon';
import { WeaponHandedness } from '../../ts/model/weapon';

    @Component
    export default class WeaponLabel extends Vue {
        @Prop()
        weapon: DisplayWeapon;

        @Prop({default: false})
        isHoverable: boolean;

        get handednessNumber() {
            if (this.weapon.handedness & WeaponHandedness.One && this.weapon.handedness & WeaponHandedness.Two) {
                return '1/2';
            } else if (this.weapon.handedness & WeaponHandedness.One) {
                return '1';
            } else if (this.weapon.handedness & WeaponHandedness.Two) {
                return '2';
            }
        }
    }
