import { Weapon, WeaponHandedness } from "../model/weapon";

const SPRADSHEET_ID = '1HXS_aF9CKeGKg6OecwCw3WobZWcTjDkp_4ipN1vnyVk';
const API_KEY = 'AIzaSyCzebhzmLja7pNdyqTjbd0Q9llFzv2tPjE';

export default class DataProvider {
    cachingDurationSeconds: number;

    constructor(cachingDurationSeconds = 30 * 60) {
        this.cachingDurationSeconds = cachingDurationSeconds;
    }

    async loadSheetData<T>(name: string) {
        const storageKey = `sheet-data-cache-${name}`;
        const cached: {expiresAt: string, data: Object} = JSON.parse(localStorage.getItem(storageKey) || 'null');

        if (cached && new Date(cached.expiresAt).getTime() >= new Date().getTime()) {
            console.info('[DataProvider] Using cached data.');
            return <T>cached.data;
        }

        const url = `https://sheets.googleapis.com/v4/spreadsheets/${SPRADSHEET_ID}/values/${name}?key=${API_KEY}`;
        const request = await fetch(url);
        const data  = <T>JSON.parse(await request.text());
        
        const expiresAt = new Date;
        expiresAt.setSeconds(expiresAt.getSeconds() + this.cachingDurationSeconds);

        localStorage.setItem(storageKey, JSON.stringify({
            data: data,
            expiresAt: expiresAt
        }));

        console.info('[DataProvider] Using fetched data.');

        return data;
    }

    private resolveWeaponHandedNess(value: string) {
        switch (value) {
            case 'One Handed':
                return WeaponHandedness.One;
            case 'Two Handed':
                return WeaponHandedness.Two;
            case 'One/Two Handed':
                return WeaponHandedness.One | WeaponHandedness.Two;
        }
    }

    async loadWeapons(): Promise<Weapon[]> {
        const data = await this.loadSheetData<{values: (string[])[]}>('Melee Weapons [Advanced]');
        const result: Weapon[] = [];

        const headerRowCount = 2;

        let rowIndex = -1;
        for (let row of data.values) {
            rowIndex++;

            if (rowIndex < headerRowCount) {
                continue;
            }
            
            const stat: Weapon = {
                id: rowIndex,
                name:       row[0],
                attackType: row[1], 
                altMode:    row[2].toLowerCase() == 'on',   
                type:       row[3],  
                handedness: this.resolveWeaponHandedNess(row[3]),                         
                stopOnHit:  row[4].toLowerCase() == 'yes',          
                canCombo:   row[5].toLowerCase() == 'yes',          
                canFlinch:  row[6].toLowerCase() == 'yes',          
                pointCost:  parseInt(row[7]),    
                loadoutPointCost: parseInt(row[8]),
                damageNoArmor: {
                    head: parseInt(row[9]),
                    torso: parseInt(row[10]),
                    legs: parseInt(row[11]),
                },
                damageLightArmor: {
                    head: parseInt(row[12]),
                    torso: parseInt(row[13]),
                    legs: parseInt(row[14]),
                },
                damageMediumArmor: {
                    head: parseInt(row[15]),
                    torso: parseInt(row[16]),
                    legs: parseInt(row[17]),
                },
                damageHeavyArmor: {
                    head: parseInt(row[18]),
                    torso: parseInt(row[19]),
                    legs: parseInt(row[20]),
                },
                computedDamage: {
                    averageDamage: parseInt(row[21]),
                    dps: parseInt(row[22]),
                    cDps: parseInt(row[23]),
                    averageDps: parseInt(row[24]),
                },
                staminaCost: {
                    miss: parseInt(row[25]),                        
                    feint: parseInt(row[26]),                        
                    morph: parseInt(row[27]),                        
                    drain: parseInt(row[28]),                        
                    parryDrainNegation: parseInt(row[29]),                        
                },
                speed: {
                    windup: parseInt(row[30]),
                    combo: parseInt(row[31]),
                    release: parseInt(row[32]),
                    recovery: parseInt(row[33]),
                },
                computedSpeed: {
                    attack: parseFloat(row[34]),
                    combo: parseFloat(row[36]),
                },
                length: parseInt(row[36])
            }

            result.push(stat);
        }

        result.sort((a: Weapon, b: Weapon) => {
            const _a = a.name + a.attackType + (a.altMode ? 'B' : 'A');
            const _b = b.name + b.attackType + (b.altMode ? 'B' : 'A');

            if (_a < _b) return -1;
            if (_a > _b) return 1;
            return 0;
        });

        return result;
    }
}