
































































































import Vue from 'vue';
import { Component, Watch } from 'vue-property-decorator';
import WeaponLabel from './partial/weapon-label.vue';
import AttributeBars from './partial/attribute-bars.vue';
import DataProvider from '../ts/data';
import {Weapon, WeaponHandedness} from '../ts/model/weapon';
import {DisplayWeapon} from '../ts/view-model/weapon';

@Component({
    components: {WeaponLabel, AttributeBars}
})
export default class App extends Vue {
    isLoading = true;
    data: {majorDimension: string, values: any[]} = null;
    dataStr = null;

    weapons: Weapon[] = [];
    displayWeapons: DisplayWeapon[] = [];

    filteredWeapons: DisplayWeapon[] = [];
    search: string = '';
    showOneHandedWeapons = true;
    showTwoHandedWeapons = true;

    highestWeaponComputedAverageDamage = 0;
    highestWeaponComputedAttackSpeed = 0;
    highestWeaponLength = 0;

    dataProvider: DataProvider;

    get selectedWeapons() {
        return this.displayWeapons.filter(o => o.isSelected);
    }

    async loadSpreadsheetData() {
        this.weapons = await this.dataProvider.loadWeapons();
        this.highestWeaponComputedAttackSpeed = Math.max(...this.weapons.map(o => o.computedSpeed.attack));
        this.highestWeaponLength = Math.max(...this.weapons.map(o => o.length));
        this.highestWeaponComputedAverageDamage = Math.max(...this.weapons.map(o => o.computedDamage.averageDamage));

        this.displayWeapons = this.weapons.map(o => new DisplayWeapon(o));
    }

    async mounted() {
        this.dataProvider = new DataProvider();
        await this.loadSpreadsheetData();       
        this.isLoading = false;     
        this.deserializeState();
        this.applyWeaponFilters();
    }

    @Watch('search')
    @Watch('showOneHandedWeapons')
    @Watch('showTwoHandedWeapons')
    @Watch('displayWeapons')
    applyWeaponFilters() {
        if (this.isLoading) {
            return;
        }

        this.serializeState();

        const search = this.search.toLowerCase();
        const result: DisplayWeapon[] = [];

        for (let i = 0; i < this.displayWeapons.length; i++) {
            const weapon = this.displayWeapons[i];

            if (
                (search.length == 0 || weapon.name.toLowerCase().indexOf(this.search) != -1) &&
                (
                    (this.showOneHandedWeapons && (weapon.handedness & WeaponHandedness.One)) ||
                    (this.showTwoHandedWeapons && (weapon.handedness & WeaponHandedness.Two))
                )
             ) {
                result.push(weapon);
            }
        }

        this.filteredWeapons = result;
    }

    @Watch('selectedWeapons')
    onSelectedWeaponsChanged() {
        if (this.isLoading) {
            return;
        }
        
        this.serializeState();
    }

    serializeState() {
        this.$router.replace({
            query: {
                ...this.$route.query,
                search: this.search,
                selected: this.selectedWeapons.map(o => o.id).join(',')
            }
        })
    }

    deserializeState() {
        this.search = this.$route.query.search as string || '';

        const selectedIds = (this.$route.query.selected as string || '').split(',').map(o => parseInt(o));

        for (let weapon of this.displayWeapons) {
            weapon.isSelected = selectedIds.indexOf(weapon.id) !== -1;
        }
    }

    selectAll() {
        for (let weapon of this.displayWeapons) {
            weapon.isSelected = true;
        }
    }

    unSelectAll() {
        for (let weapon of this.displayWeapons) {
            weapon.isSelected = false;
        }
    }
}
